import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import Layout from "../hos/Layout/Layout";
import Home from "./Home/Home";
import AllNews from "./AllNews/AllNews";
import SingleNews from "./SingleNews/SingleNews";
const publicUrl = process.env.PUBLIC_URL
class App extends Component {
  render() {
    return (
      <Switch>
        <Layout>
          <Route exact path={`${publicUrl}/`} component={Home} />
          <Route exact path={`${publicUrl}/home`} component={Home} />
          <Route exact path={`${publicUrl}/allNews`} component={AllNews} />
          <Route exact path={`${publicUrl}/singleNews`} component={SingleNews} />
        </Layout>
      </Switch>
    );
  }
}

export default App;
